.nav {
  font-family: 'Open Sans', sans-serif;
   display: flex;
   justify-content: space-between;
   align-items: center;
   background: rgb(0, 0, 0);
  }
  
  .nav-items {
   list-style: none;
   display: flex;
   color: ivory;
  }
  
  .nav-items >li {
      padding: 10px;
  }
  
  .nav-logo{
      padding-left: 10px;
      width: 60px;
  }
   body{
      background-image: url('./bg.png');
      background-repeat: no-repeat;
      background-position: 300px 100px ;
   }
  .main{
      
     display: flex;
     justify-content: space-between;
     align-items: center;
  }
  .designer>h1 , .coder>h1{
      font-family: 'Inter', sans-serif;
      font-family: 'Kdam Thmor Pro', sans-serif;
      font-size: 50px;
      padding-top: 100px ;
      padding-right: 100px;
      padding-left: 100px;
      padding-bottom: 0px;
  }
  .designer>p , .coder>p{
      font-family: 'Open Sans', sans-serif;
      color: rgb(80, 80, 80);
      padding-top: 0px;
      padding-right: 100px;
      padding-left: 100px;
  }

  .social-container {
    background: #eee;
    padding: 25px 50px;
  }
  a.social {
    margin: 0 1rem;
    transition: transform 250ms;
    display: inline-block;
  }
  a.social:hover {
    transform: translateY(-2px);
  }
  a.youtube {
    color: #eb3223;
  }
  
  a.facebook {
    color: #4968ad;
  }
  
  a.twitter {
    color: #49a1eb;
  }
  
  a.instagram {
    color: rgb(185, 111, 245);
  }

  .footer {
    text-align: center;
    position: fixed;
    padding: 10px 10px 0px 10px;
    bottom: 0;
    width: 100%;
    /* Height of the footer*/ 
    height: 40px;
    
}